<template>
  <div class="containerFrame">
    <div class="tabBox">
      <v-tabs v-model="tabIdx">
        <v-tab v-for="item in tabs" :key="item">
          {{ item }}
        </v-tab>
      </v-tabs>
    </div>
    <div class="fix">
      <!-- Vessel -->
      <DetailComponent
        key="Voyage History"
        v-if="tabs[tabIdx] === 'Voyage History'"
        :obj="{
          categoryOption: {
            api: ['ship', 'voyage_history_GetSelectedShip_Combo'],
            textKey: 'ship_name',
            valueKey: 'ship_idx',
            modelKey: 'ship_idx',
          },

          disbleNewItem: true,
          detailProp: 'Ship History Detail',

          masterApi: ['voyage', 'voyage_history'],
          masterName: 'Voyage History',
          masterHeaders: [
            {
              text: 'voyage_history_idx',
              value: 'voyage_history_idx',
              align: 'center',
            },
            {
              text: 'imo_id',
              value: 'imo_id',
              align: 'center',
              type: 'text',
            },
            {
              text: 'departure_port_name',
              value: 'departure_port_name',
              align: 'center',
              type: 'text',
            },
            {
              text: 'departure_time',
              value: 'departure_time',
              align: 'center',
              type: 'text',
            },
            {
              text: 'arrival_port_name',
              value: 'arrival_port_name',
              align: 'center',
              type: 'text',
            },
            {
              text: 'arrival_time',
              value: 'arrival_time',
              align: 'center',
              type: 'text',
            },
            { text: '', value: 'icon', sortable: false },
          ],
        }"
      />
    </div>
  </div>
</template>

<script>
import { ref } from "vue";
import DetailComponent from "@/components/DetailComponent.vue";
import { useMenuStore } from "@/stories/mainStore.js";

export default {
  name: "VoyageView",
  components: {
    DetailComponent,
  },
  setup() {
    //
    const menuStore = useMenuStore();
    const tabIdx = ref(0);

    const tabsMap = {
      voyage_history: "Voyage History",
    };

    const tabArr = [];
    menuStore.tabs.get(location.pathname.replace(/\//, "")).map((item) => {
      if (!tabsMap[item.name]) {
        console.warn(`누락 ${item.name}`);
      } else {
        tabArr.push(tabsMap[item.name]);
      }
    });
    const tabs = ref(tabArr);

    return {
      tabs,
      tabIdx,
    };
  },
};
</script>

<style></style>
